import { createRouter, createWebHashHistory } from 'vue-router'
import Login from '../views/login.vue'
import Index from '../views/blogs/index'
import Jsonviewblog from '../views/blogs/_vue/jsonviewblog'
import Image2Text_by_opencv_blog from '../views/blogs/_java/Image2Text_by_opencv_blog'

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/index',
    name: 'Index',
    component: Index,
    children: [
      {
        path: '/vue',
        name: 'Jsonviewblog',
        component: Jsonviewblog
      },
      {
        path: '/java',
        name: 'Image2Text_by_opencv_blog',
        component: Image2Text_by_opencv_blog
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
