<template>
    <h2>vue前端使用json-viewer进行json展示</h2>
    <article class="baidu_pl" style="text-align: left">
    <div id="article_content" class="article_content clearfix">
    <link rel="stylesheet" href="../../../css/csdn/ck_htmledit_views-b5506197d8.css">
    <div id="content_views" class="markdown_views prism-atom-one-dark">
    <svg xmlns="http://www.w3.org/2000/svg" style="display: none;">
    <path stroke-linecap="round" d="M5,0 0,2.5 5,5z" id="raphael-marker-block" style="-webkit-tap-highlight-color: rgba(0, 0, 0, 0);"></path>
    </svg>
    <p>第一步：$ npm install vue-json-viewer --save 进行下载<br> 第二步：在配置文件中导入，import JsonViewer from ‘vue-json-viewer’，并使用Vue.use(JsonViewer)<br> 第三步：直接使用json-viewer标签<br> 属性说明：</p>
    <div class="table-box"><table><thead><tr><th>属性</th><th>描述</th><th>默认值</th></tr></thead><tbody><tr><td>expand-depth</td><td>展开层数</td><td>1</td></tr><tr><td>value</td><td>数据源json串</td><td>必填json串</td></tr><tr><td>copyable</td><td>复制按钮</td><td>false</td></tr><tr><td>sort</td><td>排序</td><td>false</td></tr></tbody></table></div>
    <p>例如：<code>&lt;json-viewer :value="jsonData" :expand-depth=1 copyable&gt;&lt;/json-viewer&gt;</code></p>
