<template>
<h2>java利用opencv实现用字符展示视频或图片</h2>
<article class="baidu_pl" style="text-align: left">
<div id="article_content" class="article_content clearfix">
<link rel="stylesheet" href="../../../css/csdn/ck_htmledit_views-b5506197d8.css">
<div id="content_views" class="markdown_views prism-atom-one-dark">
<svg xmlns="http://www.w3.org/2000/svg" style="display: none;">
<path stroke-linecap="round" d="M5,0 0,2.5 5,5z" id="raphael-marker-block" style="-webkit-tap-highlight-color: rgba(0, 0, 0, 0);"></path>
</svg>
<p>背景：前段时间看到有人将图片转成符号，感觉挺有意思的，就结合了一下opencv。<br> 代码如下：</p>
<pre class="prettyprint"><code class="prism language-java has-numbering" onclick="mdcp.copyCode(event)" style="position: unset;"><span class="token keyword">package</span> org<span class="token punctuation">.</span>fxd<span class="token punctuation">.</span>utils<span class="token punctuation">;</span>

<span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>core<span class="token punctuation">.</span>Mat<span class="token punctuation">;</span>
<span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>imgproc<span class="token punctuation">.</span>Imgproc<span class="token punctuation">;</span>
<span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>videoio<span class="token punctuation">.</span>VideoCapture<span class="token punctuation">;</span>

<span class="token keyword">import</span> javax<span class="token punctuation">.</span>imageio<span class="token punctuation">.</span>ImageIO<span class="token punctuation">;</span>
<span class="token keyword">import</span> javax<span class="token punctuation">.</span>swing<span class="token punctuation">.</span>*<span class="token punctuation">;</span>
<span class="token keyword">import</span> java<span class="token punctuation">.</span>awt<span class="token punctuation">.</span>*<span class="token punctuation">;</span>
<span class="token keyword">import</span> java<span class="token punctuation">.</span>awt<span class="token punctuation">.</span>image<span class="token punctuation">.</span>BufferedImage<span class="token punctuation">;</span>
<span class="token keyword">import</span> java<span class="token punctuation">.</span>io<span class="token punctuation">.</span>*<span class="token punctuation">;</span>

<span class="token comment">/**
 * 将图片转成text文件输出
 * @author Litluecat
 */</span>
<span class="token keyword">public</span> <span class="token keyword">class</span> <span class="token class-name">Image2Text</span> <span class="token punctuation">{<!-- --></span>

    <span class="token keyword">static</span> <span class="token punctuation">{<!-- --></span>
        <span class="token comment">//opencv_java410.dll的所在地址，自己换成自己dll的地址</span>
        System<span class="token punctuation">.</span><span class="token function">load</span><span class="token punctuation">(</span><span class="token string">"D:\\Sofeware\\opencv\\build\\java\\x64\\opencv_java410.dll"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>

    <span class="token comment">/** 此处设置灰度字符*/</span>
    <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">char</span><span class="token punctuation">[</span><span class="token punctuation">]</span> cs <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">char</span><span class="token punctuation">[</span><span class="token punctuation">]</span> <span class="token punctuation">{<!-- --></span><span class="token string">'0'</span><span class="token punctuation">,</span><span class="token string">'1'</span><span class="token punctuation">,</span><span class="token string">'.'</span><span class="token punctuation">,</span><span class="token string">' '</span><span class="token punctuation">}</span><span class="token punctuation">;</span>

    <span class="token keyword">public</span> <span class="token keyword">static</span> <span class="token keyword">void</span> <span class="token function">main</span><span class="token punctuation">(</span>String<span class="token punctuation">[</span><span class="token punctuation">]</span> args<span class="token punctuation">)</span> <span class="token keyword">throws</span> IOException <span class="token punctuation">{<!-- --></span>
        <span class="token comment">//String imageUrl = "E:\\1.png";</span>
        <span class="token comment">// 设置JTextArea，用于展示字符</span>
        JTextArea textArea <span class="token operator">=</span> <span class="token function">getJFrame</span><span class="token punctuation">(</span><span class="token string">"XXTop"</span><span class="token punctuation">,</span> <span class="token string">"宋体"</span><span class="token punctuation">,</span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">,</span> <span class="token number">900</span><span class="token punctuation">,</span> <span class="token number">900</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">// 将图片转成字符</span>
        <span class="token comment">//textArea.setText(image2Text(imageUrl,1).toString());</span>
        <span class="token comment">//将视频转成字符</span>
        <span class="token function">video2TextByOpenCV</span><span class="token punctuation">(</span>textArea<span class="token punctuation">,</span> <span class="token string">"E:\\1.mp4"</span><span class="token punctuation">,</span> <span class="token number">1</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>

    <span class="token comment">/**
     * 生成JFrame窗口，并返回JTextArea对象
     * @param frameTitle 窗口标题
     * @param fontName 文本框中字体类型
     * @param fontStyle 文本框中字体格式
     * @param fontSize 文本框中字体大小
     * @param JFWideth 窗口宽度
     * @param JFHeight 窗口高度
     * @return
     */</span>
    <span class="token keyword">public</span> <span class="token keyword">static</span> JTextArea <span class="token function">getJFrame</span><span class="token punctuation">(</span>String frameTitle<span class="token punctuation">,</span>String fontName<span class="token punctuation">,</span> <span class="token keyword">int</span> fontStyle<span class="token punctuation">,</span> <span class="token keyword">int</span> fontSize<span class="token punctuation">,</span> <span class="token keyword">int</span> JFWideth<span class="token punctuation">,</span> <span class="token keyword">int</span> JFHeight<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
        JFrame frame <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">JFrame</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//创建一个窗口对象</span>
        JPanel panel <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">JPanel</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        JTextArea textArea <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">JTextArea</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">// name:字体类型 style:0,表示字形;1,粗体;2,斜体   size:字体大小</span>
        textArea<span class="token punctuation">.</span><span class="token function">setFont</span><span class="token punctuation">(</span><span class="token keyword">new</span> <span class="token class-name">Font</span><span class="token punctuation">(</span>fontName<span class="token punctuation">,</span>fontStyle<span class="token punctuation">,</span>fontSize<span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        panel<span class="token punctuation">.</span><span class="token function">setLayout</span><span class="token punctuation">(</span><span class="token keyword">new</span> <span class="token class-name">GridLayout</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//当TextArea里的内容过长时生成滚动条</span>
        panel<span class="token punctuation">.</span><span class="token function">add</span><span class="token punctuation">(</span><span class="token keyword">new</span> <span class="token class-name">JScrollPane</span><span class="token punctuation">(</span>textArea<span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        frame<span class="token punctuation">.</span><span class="token function">add</span><span class="token punctuation">(</span>panel<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//对窗口设置标题</span>
        frame<span class="token punctuation">.</span><span class="token function">setTitle</span><span class="token punctuation">(</span>frameTitle<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//设置窗口的大小</span>
        frame<span class="token punctuation">.</span><span class="token function">setSize</span><span class="token punctuation">(</span>JFWideth<span class="token punctuation">,</span>JFHeight<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//设置窗口界面的关闭按钮真的生效(也可以直接传一个3进去，JFrame.EXIT_ON_CLOSE==3，效果一样)</span>
        frame<span class="token punctuation">.</span><span class="token function">setDefaultCloseOperation</span><span class="token punctuation">(</span>JFrame<span class="token punctuation">.</span>EXIT_ON_CLOSE<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//设置窗口居中显示</span>
        frame<span class="token punctuation">.</span><span class="token function">setLocationRelativeTo</span><span class="token punctuation">(</span>null<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//设置窗口置顶显示</span>
        frame<span class="token punctuation">.</span><span class="token function">setAlwaysOnTop</span><span class="token punctuation">(</span><span class="token boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//设置窗口显示</span>
        frame<span class="token punctuation">.</span><span class="token function">setVisible</span><span class="token punctuation">(</span><span class="token boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">return</span> textArea<span class="token punctuation">;</span>
    <span class="token punctuation">}</span>

    <span class="token comment">/**
     * OpenCV-4.1.0 从视频文件中读取
     * @param textArea JTextArea文本框对象
     * @param targetImgUrl 视频地址
     * @param img2TextSzie 图片转文本缩小比例
     */</span>
    <span class="token keyword">public</span> <span class="token keyword">static</span> <span class="token keyword">void</span> <span class="token function">video2TextByOpenCV</span><span class="token punctuation">(</span>JTextArea textArea<span class="token punctuation">,</span> String targetImgUrl<span class="token punctuation">,</span> <span class="token keyword">int</span> img2TextSzie<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
        VideoCapture capture<span class="token operator">=</span><span class="token keyword">new</span> <span class="token class-name">VideoCapture</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//1 读取视频文件的路径</span>
        capture<span class="token punctuation">.</span><span class="token function">open</span><span class="token punctuation">(</span>targetImgUrl<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">if</span><span class="token punctuation">(</span><span class="token operator">!</span>capture<span class="token punctuation">.</span><span class="token function">isOpened</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
            System<span class="token punctuation">.</span>out<span class="token punctuation">.</span><span class="token function">println</span><span class="token punctuation">(</span><span class="token string">"读取视频文件失败！"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token keyword">return</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span>
        Mat video<span class="token operator">=</span><span class="token keyword">new</span> <span class="token class-name">Mat</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">while</span><span class="token punctuation">(</span>capture<span class="token punctuation">.</span><span class="token function">isOpened</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
            <span class="token comment">//2 视频文件的视频写入 Mat video 中</span>
            capture<span class="token punctuation">.</span><span class="token function">read</span><span class="token punctuation">(</span>video<span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token keyword">try</span><span class="token punctuation">{<!-- --></span>
                textArea<span class="token punctuation">.</span><span class="token function">setText</span><span class="token punctuation">(</span><span class="token function">image2TextByOpenCV</span><span class="token punctuation">(</span>video<span class="token punctuation">,</span>img2TextSzie<span class="token punctuation">)</span><span class="token punctuation">.</span><span class="token function">toString</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span><span class="token keyword">catch</span> <span class="token punctuation">(</span><span class="token class-name">Exception</span> e<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
                System<span class="token punctuation">.</span>out<span class="token punctuation">.</span><span class="token function">println</span><span class="token punctuation">(</span><span class="token string">"图片识别异常"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                <span class="token keyword">break</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span>
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span>

    <span class="token comment">/**
     *基于openCV实现
     * @param image 待处理Mat图片(视频中的某一帧)
     * @param size 对结果进行缩小，1为不缩小
     */</span>
    <span class="token keyword">public</span> <span class="token keyword">static</span> StringBuffer <span class="token function">image2TextByOpenCV</span><span class="token punctuation">(</span>Mat image<span class="token punctuation">,</span><span class="token keyword">int</span> size<span class="token punctuation">)</span> <span class="token keyword">throws</span> Exception<span class="token punctuation">{<!-- --></span>
        StringBuffer text <span class="token operator">=</span> null<span class="token punctuation">;</span>
        Mat img_mat <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Mat</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">//mat表示要要转换的图片[Mat类型],img_mat表示转换后的图片</span>
        Imgproc<span class="token punctuation">.</span><span class="token function">cvtColor</span><span class="token punctuation">(</span>image<span class="token punctuation">,</span> img_mat<span class="token punctuation">,</span> Imgproc<span class="token punctuation">.</span>COLOR_RGB2GRAY<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">int</span> rows <span class="token operator">=</span> img_mat<span class="token punctuation">.</span><span class="token function">rows</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">int</span> cols <span class="token operator">=</span> img_mat<span class="token punctuation">.</span><span class="token function">cols</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">// 图片转字符串后的数组</span>
        <span class="token keyword">char</span><span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">[</span><span class="token punctuation">]</span> css <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">char</span><span class="token punctuation">[</span>rows<span class="token operator">/</span>size <span class="token operator">+</span> <span class="token number">1</span><span class="token punctuation">]</span><span class="token punctuation">[</span>cols<span class="token operator">/</span>size <span class="token operator">+</span> <span class="token number">1</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
        <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">int</span> x<span class="token operator">=</span><span class="token number">0</span><span class="token punctuation">;</span> x<span class="token operator">&lt;</span>rows<span class="token punctuation">;</span> x<span class="token operator">+=</span>size<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
            <span class="token keyword">for</span> <span class="token punctuation">(</span><span class="token keyword">int</span> y<span class="token operator">=</span><span class="token number">0</span><span class="token punctuation">;</span> y<span class="token operator">&lt;</span>cols<span class="token punctuation">;</span> y<span class="token operator">+=</span>size<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
                <span class="token comment">//获得图片的灰度值  x,y 表示像素点的位置(姑且理解为像素点吧)</span>
                <span class="token keyword">int</span> gray <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token keyword">int</span><span class="token punctuation">)</span> img_mat<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span>x<span class="token punctuation">,</span> y<span class="token punctuation">)</span><span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
                <span class="token comment">// 得到灰度值</span>
                <span class="token keyword">int</span> index <span class="token operator">=</span> Math<span class="token punctuation">.</span><span class="token function">round</span><span class="token punctuation">(</span><span class="token punctuation">(</span>gray <span class="token operator">*</span> cs<span class="token punctuation">.</span>length <span class="token operator">-</span><span class="token number">1</span><span class="token punctuation">)</span> <span class="token operator">/</span> <span class="token number">255</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                css<span class="token punctuation">[</span>x<span class="token operator">/</span>size<span class="token punctuation">]</span><span class="token punctuation">[</span>y<span class="token operator">/</span>size<span class="token punctuation">]</span> <span class="token operator">=</span> cs<span class="token punctuation">[</span>index<span class="token punctuation">]</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span>
        <span class="token punctuation">}</span>
        StringBuffer sb <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">StringBuffer</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token comment">// 开始拼接内容</span>
        <span class="token keyword">for</span> <span class="token punctuation">(</span><span class="token keyword">int</span> x <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">;</span> x <span class="token operator">&lt;</span> css<span class="token punctuation">.</span>length<span class="token punctuation">;</span> x<span class="token operator">++</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
            <span class="token keyword">for</span> <span class="token punctuation">(</span><span class="token keyword">int</span> y <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">;</span> y <span class="token operator">&lt;</span> css<span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span><span class="token punctuation">.</span>length<span class="token punctuation">;</span> y<span class="token operator">++</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
                sb<span class="token punctuation">.</span><span class="token function">append</span><span class="token punctuation">(</span>css<span class="token punctuation">[</span>x<span class="token punctuation">]</span><span class="token punctuation">[</span>y<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span>
            sb<span class="token punctuation">.</span><span class="token function">append</span><span class="token punctuation">(</span><span class="token string">"\r\n"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span>
        <span class="token keyword">return</span> sb<span class="token punctuation">;</span>
    <span class="token punctuation">}</span>

    <span class="token comment">/**
     * @param imageUrl 图片地址
     * @param size 对结果进行缩小，1为不缩小
     */</span>
    <span class="token keyword">public</span> <span class="token keyword">static</span> StringBuffer <span class="token function">image2Text</span><span class="token punctuation">(</span>String imageUrl<span class="token punctuation">,</span><span class="token keyword">int</span> size<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
        StringBuffer text <span class="token operator">=</span> null<span class="token punctuation">;</span>
        <span class="token keyword">try</span> <span class="token punctuation">{<!-- --></span>
            BufferedImage image <span class="token operator">=</span> ImageIO<span class="token punctuation">.</span><span class="token function">read</span><span class="token punctuation">(</span><span class="token keyword">new</span> <span class="token class-name">File</span><span class="token punctuation">(</span>imageUrl<span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token keyword">int</span> width <span class="token operator">=</span> image<span class="token punctuation">.</span><span class="token function">getWidth</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token keyword">int</span> height <span class="token operator">=</span> image<span class="token punctuation">.</span><span class="token function">getHeight</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token comment">// 图片转字符串后的数组</span>
            <span class="token keyword">char</span><span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">[</span><span class="token punctuation">]</span> css <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">char</span><span class="token punctuation">[</span>width<span class="token operator">/</span>size <span class="token operator">+</span> <span class="token number">1</span><span class="token punctuation">]</span><span class="token punctuation">[</span>height<span class="token operator">/</span>size <span class="token operator">+</span> <span class="token number">1</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
            <span class="token keyword">for</span> <span class="token punctuation">(</span><span class="token keyword">int</span> x <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">;</span> x <span class="token operator">&lt;</span> width<span class="token punctuation">;</span> x<span class="token operator">+=</span>size<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
